      state.totalCount = 0; //汇总数量清0
      state.totalPrice = 0;
      if (state.tableData.some(function (item) {
         return item.goodsId == goods.goodsId;
      })) {
         state.tableData.filter(function (item) {
            return item.goodsId == goods.goodsId;
         })[0].count++;
      } else {
         state.tableData.push({
            goodsId: goods.goodsId,
            goodsName: goods.goodsName,
            price: goods.price,
            count: 1
         });
      }

      state.tableData.forEach(function (item) {
         state.totalCount += item.count;
         state.totalPrice += item.count * item.price;
      });
